import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Box from '@material-ui/core/Box';
import Container from '@material-ui/core/Container';
import MenuAdmin from '../../../components/menu-admin';
import Copyright from '../../../components/footer';
import GridList from '@material-ui/core/GridList';

import Divider from '@material-ui/core/Divider';
import MoreVertIcon from "@material-ui/icons/MoreVert";
import ImageIcon from "@material-ui/icons/Image";
import WorkIcon from "@material-ui/icons/Work";
import BeachAccessIcon from "@material-ui/icons/BeachAccess";
import {
    Paper,
    Typography,
    Grid,
    Card,
    CardHeader,
    CardContent,
    Avatar,
    List,
    ListItem,
    ListItemText,
    IconButton
} from "@material-ui/core";

const list = [
    { id: 1, name: "Kelvin", text: "Lorem ipsum", image: <ImageIcon /> },
    { id: 2, name: "Robson", text: "Lorem ipsum", image: <WorkIcon /> },
    { id: 3, name: "Cleiton", text: "Lorem ipsum", image: <BeachAccessIcon /> },
    { id: 3, name: "Cleiton", text: "Lorem ipsum", image: <BeachAccessIcon /> },
    { id: 3, name: "Cleiton", text: "Lorem ipsum", image: <BeachAccessIcon /> },
    { id: 3, name: "Cleiton", text: "Lorem ipsum", image: <BeachAccessIcon /> },
    { id: 3, name: "Cleiton", text: "Lorem ipsum", image: <BeachAccessIcon /> },
    { id: 3, name: "Cleiton", text: "Lorem ipsum", image: <BeachAccessIcon /> },
    { id: 3, name: "Cleiton", text: "Lorem ipsum", image: <BeachAccessIcon /> },

];

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
    },
    fila: {
        zIndex: 999,
        position: "relative"
    },
    toolbar: {
        paddingRight: 24, // keep right padding when drawer closed
    },
    toolbarIcon: {
        alignItems: 'left',
        justifyContent: 'flex-end',
        padding: '0 8px',
        ...theme.mixins.toolbar,
    },
    appBar: {
        zIndex: theme.zIndex.drawer + 1,
        transition: theme.transitions.create(['width', 'margin'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
    },
    appBarShift: {
        marginLeft: drawerWidth,
        width: `calc(100% - ${drawerWidth}px)`,
        transition: theme.transitions.create(['width', 'margin'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    menuButton: {
        marginRight: 36,
    },
    menuButtonHidden: {
        display: 'none',
    },
    title: {
        flexGrow: 1,
    },
    drawerPaper: {
        position: 'relative',
        whiteSpace: 'nowrap',
        width: drawerWidth,
        transition: theme.transitions.create('width', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    card: {
        display: "flex",
        height: "calc(91.05vh - 100px)",
        width: `calc(110% - ${drawerWidth}px)`,
    },
    rightBorder: {
        borderRight: "solid #d0D0D0 1px"
    },
    content: {
        marginTop: 0
    },
    background: {
        position: "absolute",
        height: 200,
        width: "100%",
        top: 0,
        background: "#7159C1"
    },
    rightContainer: {
        background:
            "url(/wall.jpg) center center",
        flex: 1
    },
    heightAdjust: {
        display: "flex",
        flexDirection: "column"
    },
    paper: {
        background: "#9de1fe",
        padding: 20
    },
    information: {
        color: "#444"
    },
    drawerPaperClose: {
        overflowX: 'hidden',
        transition: theme.transitions.create('width', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
        width: theme.spacing(7),
        [theme.breakpoints.up('sm')]: {
            width: theme.spacing(9),
        },
    },
    list: {
        width: '100%',
        paddingLeft: 15
    },
    gridList: {
        width: "10vw"
    },
    appBarSpacer: theme.mixins.toolbar,
    content: {
        flexGrow: 1,
        height: '100vh',
        overflow: 'auto',
    },
    container: {
        paddingTop: theme.spacing(4),
        paddingBottom: theme.spacing(4),
    },
    paper: {
        padding: theme.spacing(2),
        display: 'flex',
        overflow: 'auto',
        flexDirection: 'column',
    },
    warnings: {
        background: "#9de1fe",
        padding: 20
    },
    fixedHeight: {
        height: 240,
    },
}));

export default function WhatsApp() {
    const classes = useStyles();
    return (
        <div className={classes.root}>
            <CssBaseline />

            <MenuAdmin name="WhatsApp" />

            <main className={classes.content}>

                <div className={classes.appBarSpacer} />

                <Container maxWidth="80vh" className={classes.container}>

                    <Grid>

                        <Grid container className={classes.fila}>

                            <Grid item xs={12}>

                                <Card className={classes.card}>

                                    <Grid container>
                                            <Grid item xs={3}>
                                                <CardHeader
                                                    className={classes.rightBorder}
                                                    avatar={
                                                        <Avatar aria-label="Recipe" className={classes.avatar} src={'data:image/jpeg;base64,'}></Avatar>
                                                    }
                                                    title='Niedson'
                                                />

                                                <Paper className={classes.warnings} elevation={0}>
                                                    <Typography className={classes.information} variant="subheader">
                                                        Acesse nossa comunidade no Discord e fique por dentro das novidades!
                                                    </Typography>
                                                </Paper>
                                                <GridList className={classes.gridList}>
                                                <List className={classes.list}>
                                                    {list.map(item => (
                                                        <>
                                                            <ListItem>
                                                                <Avatar>{item.image}</Avatar>
                                                                <ListItemText className={classes.list} primary={item.name} secondary={item.text} />
                                                            </ListItem>
                                                            <Divider variant="inset" component="li" />
                                                        </>
                                                    ))}
                                                </List>
                                                </GridList>
                                            </Grid>
                                        

                                        <Grid className={classes.heightAdjust} item xs={9}>
                                            <CardHeader
                                                avatar={
                                                    <Avatar aria-label="Recipe" className={classes.avatar}>
                                                        <ImageIcon />
                                                    </Avatar>
                                                }
                                                action={
                                                    <IconButton>
                                                        <MoreVertIcon />
                                                    </IconButton>
                                                }
                                                title="Niedson"
                                            />
                                            <CardContent className={[classes.rightContainer, classes.content]} />
                                            <Grid>
                                                <h2>hhh</h2>
                                            </Grid>
                                        </Grid>

                                    </Grid>

                                </Card>

                            </Grid>

                        </Grid>

                    </Grid>

                    <Box pt={4}>
                        <Copyright />
                    </Box>

                </Container>

            </main>
        </div >
    );
}